@import url('https://fonts.googleapis.com/css?family=McLaren&display=swap');

.Startmenu {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.Menu {
	text-align: center;
}

.Playerbutton {
	font-family: 'McLaren', cursive;
	background-color: #41C8FC;
	border-color: #41C8FC;
	border-radius: 30%;
	width: 20vw;
	height: 13vh;
	margin: 3%;
}

.Playerbutton:hover {
	background-color: #41ECFC;
	border-color: #41ECFC;
}

.Title {
	font-family: 'McLaren', cursive;
	font-size: 10vh;
}