@import url(https://fonts.googleapis.com/css?family=McLaren&display=swap);
@import url(https://fonts.googleapis.com/css?family=McLaren&display=swap);
body {
  background-color: #fff6e3;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Gameboard{
  background-image: url(data:image/png;base64,);
  background-size: 90vh 70vh;
  height: 70vh;
  width: 90vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  z-index: 0;
  border-style: solid;
  border-width: 5px;
  border-color: #1C91FF;
}

.Hole {
	background-color: transparent;
	position: relative;
	top: 0.4vh;
	display: flex;
	justify-content: space-around;
	height: 12vh;
}

.Board{
  margin-left: auto;
  margin-right: auto;
}

.Piece {
	text-align: center;
	height: 11.2vh;
	width: 11.2vh;
}

.Column {
	display: flex;
	width: 12vw;
	height: 70vh;
	flex-direction: column;
	justify-content: space-around;
}

.Gohere {
	width: 90vh;
 	display: flex;
	flex-direction: row;
	justify-content: space-around;
}

.Nextpiece {
	text-align: center;
	height: 10.4vh;
	width: 10.4vh;
	opacity: 0;
}

.Nextpiece:hover {
	height: 10vh;
	opacity: 100;
}

.Redbutton {
	background-color: red;
	border-color: red;
	height: 10.45vh;
	width: 10.45vh;
	border-radius: 50%;
}

.Yellowbutton {
	background-color: yellow;
	border-color: yellow;
	height: 10.4vh;
	width: 10.4vh;
	border-radius: 50%;
}

.Game {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.Main {
	width: 90vh;
	display: grid;
	grid-template-columns: 20vh 80vh;
}

.Sidebutton {
  background-color: #ff5b24;
  border-color: #e85320;
  border-radius: 40%;
  height: 15%;
  width: 80%;
	font-family: 'McLaren', cursive;
  font-size: 140%;
}

h1 {
	font-family: 'McLaren', cursive;
	font-size: 300%;
	margin: 0;
}

.Sidebar {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.Startmenu {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.Menu {
	text-align: center;
}

.Playerbutton {
	font-family: 'McLaren', cursive;
	background-color: #41C8FC;
	border-color: #41C8FC;
	border-radius: 30%;
	width: 20vw;
	height: 13vh;
	margin: 3%;
}

.Playerbutton:hover {
	background-color: #41ECFC;
	border-color: #41ECFC;
}

.Title {
	font-family: 'McLaren', cursive;
	font-size: 10vh;
}
